.notice {
  background: tomato;
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  padding: 1rem;
  border-radius: 3px;
  color: white;
}

.notice span {
  padding-left: 1rem;
  text-decoration: underline;
  cursor: pointer;
}

.notice span:hover {
  opacity: 0.7;
}

.meetups {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding-top: 50px;
}

.description {
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 1rem;
  opacity: 0.85;
  text-align: center;
  line-height: 150%;
}

.members {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding-top: 30px;
  padding-bottom: 60px;
}

.link {
  color: dodgerblue;
  padding: 1rem;
  border-radius: 3px;
}

.link:hover {
  background: rgba(30, 143, 255, 0.089);
}


