.member .img {
  width: 50px;
  height: 50px;
  background-position: center;
  background-size: cover;
  -webkit-transition: 0.1s ease-in-out 0s;
  -o-transition: 0.1s ease-in-out 0s;
  transition: 0.1s ease-in-out 0s;
  margin: 0.25rem;
  border-radius: 3px;
}

.member .img:hover {
  -webkit-transition: 0.1s ease-in-out 0s;
  -o-transition: 0.1s ease-in-out 0s;
  transition: 0.1s ease-in-out 0s;
  opacity: 0.5;
}


