header {
  min-height: 70vh;
  margin-top: 2.5%;
  margin: 0 auto;
  background-image: url("../../images/meetup-image.png");
  background-position: center;
  background-size: cover;
}

header h4 {
  font-family: "IBM Plex Sans", sans-serif;
  font-style: italic;
  text-align: center;
  padding-top: 2rem;
  color: white;
  font-weight: 600;
  position: absolute;
  font-size: 2em;
  left: 50%;
  -webkit-transform: translateX(-50%);
          -ms-transform: translateX(-50%);
      transform: translateX(-50%);
}

header .header-content {
  min-height: 70vh;
  width: 100%;
  background: rgba(53, 53, 53, 0.45);
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding-left: 1rem;
  padding-right: 1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

header .header-content .illustration {
  width: 120px;
  position: absolute;
  top: 30%;
  left: 10%;
}

@media (max-width: 768px) {
  header .header-content .illustration {
    display: none;
  }
}

header .header-content .illustration2 {
  width: 120px;
  position: absolute;
  top: 50%;
  left: 28%;
}

@media (max-width: 768px) {
  header .header-content .illustration2 {
    display: none;
  }
}

header .header-content .illustration3 {
  width: 120px;
  position: absolute;
  top: 15%;
  right: 10%;
}

@media (max-width: 768px) {
  header .header-content .illustration3 {
    display: none;
  }
}

header .header-content h1 {
  font-size: 3.25em;
  color: white;
  text-align: center;
  font-weight: 600;
  font-family: "IBM Plex Sans", sans-serif;
  letter-spacing: 1.5px;
  max-width: 1000px;
  line-height: 130%;
  z-index: 1000 !important;
}

@media (max-width: 768px) {
  header .header-content h1 {
    padding-top: 3rem;
    font-size: 2.5em;
  }
}

header .header-content button {
  margin-top: 2rem;
}

header .header-content .status-container {
  position: fixed;
  bottom: 0;
  left: 0;
  background: mediumaquamarine;
  color: white;
  padding: 1rem;
  width: 100%;
  z-index: 100;
}

header .header-content .status-container h3 {
  font-size: 0.9em;
  padding: 0;
}

header .header-content .status-container-err {
  position: fixed;
  bottom: 0;
  left: 0;
  background: orangered;
  color: white;
  width: 100%;
  padding: 1rem;
  z-index: 100;
}

header .header-content .status-container-err h3 {
  font-size: 0.9em;
  padding: 0;
}


