.meetup-card {
  padding: 1.5rem;
  border: 1px dashed #d6d6d6;
  border-radius: 3px;
  margin-bottom: 2rem;
}

.meetup-card:hover {
  background: rgba(30, 143, 255, 0.041);
}

.meetup-card .info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  margin-bottom: 1rem;
}

.meetup-card .info h3 {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  border-radius: 3px;
  background: rgba(30, 143, 255, 0.027);
  border: 1px solid #d8d8d8;
  text-align: center;
  height: 55px;
  width: 55px;
  font-size: 1em;
  text-transform: capitalize;
  font-weight: 500;
  margin-right: 1rem;
}

@media (max-width: 600px) {
  .meetup-card .info h3 {
    display: none;
  }
}

.meetup-card .info h3 span {
  font-weight: 500;
}

.meetup-card .info h4 {
  padding-top: 0.3rem;
  opacity: 0.6;
}

.meetup-card p {
  line-height: 25px;
}


